import React, { useState } from "react";
import { styled } from "@mui/material/styles";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Typography } from "@mui/material";
import DownloadIcon from "@mui/icons-material/Download";
import TablePagination from "@mui/material/TablePagination";
import FirstPageIcon from "@mui/icons-material/FirstPage";
import KeyboardArrowLeft from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRight from "@mui/icons-material/KeyboardArrowRight";
import LastPageIcon from "@mui/icons-material/LastPage";
import { useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
// import { format } from "date-fns";
import FilterModal from "./Filter";
import { PDFDownloadLink } from "@react-pdf/renderer";
import MyDocument from "./PdfRenderer";
import { Grid, Button } from "@mui/material";
import IconButton from "@mui/material/IconButton";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import Modal from "@mui/material/Modal";
import ShiftForm from "../common/shiftForm";
// import { utcToZonedTime } from "date-fns-tz";
const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));

const getTimeStamp = (startTime, endTime) => {
  const formatTime = (timeString) => {
    const date = new Date(timeString);
    return date.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
  };

  const formattedStartTime = formatTime(startTime);
  const formattedEndTime = formatTime(endTime);

  const formattedTimeRange = `${formattedStartTime} - ${formattedEndTime}`;
  return formattedTimeRange;
};

const convertDate = (date) => {
  const datePart = date.split("T")[0];
  const [year, month, day] = datePart.split("-");
  const formattedDate = `${month}-${day}-${year}`;
  return formattedDate;
};

function TablePaginationActions(props) {
  const theme = useTheme();
  const page = props?.state?.page;
  const rowsperpage = props?.state?.rowsperpage;
  const count = props?.ShiftData?.shiftList?.count;

  const handleFirstPageButtonClick = (event) => {
    props.handleChangePage(event, 0);
  };

  const handleBackButtonClick = (event) => {
    props.handleChangePage(event, page - 1);
  };

  const handleNextButtonClick = (event) => {
    props.handleChangePage(event, page + 1);
  };

  const handleLastPageButtonClick = (event) => {
    props.handleChangePage(
      event,
      Math.max(0, Math.ceil(count / rowsperpage) - 1)
    );
  };

  return (
    <Box sx={{ flexShrink: 0, ml: 2.5 }}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        {theme.direction === "rtl" ? <LastPageIcon /> : <FirstPageIcon />}
      </IconButton>
      <IconButton
        onClick={handleBackButtonClick}
        disabled={page === 0}
        aria-label="previous page"
      >
        {theme.direction === "rtl" ? (
          <KeyboardArrowRight />
        ) : (
          <KeyboardArrowLeft />
        )}
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsperpage) - 1}
        aria-label="next page"
      >
        {theme.direction === "rtl" ? (
          <KeyboardArrowLeft />
        ) : (
          <KeyboardArrowRight />
        )}
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsperpage) - 1}
        aria-label="last page"
      >
        {theme.direction === "rtl" ? <FirstPageIcon /> : <LastPageIcon />}
      </IconButton>
    </Box>
  );
}

const ShiftListPage = (props) => {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    bgcolor: "background.paper",
    boxShadow: 24,
    p: 2,
  };

  const handleGeneratePDF = () => {
    const blob = new Blob([<MyDocument data={props?.ShiftData?.shiftList} />], {
      type: "application/pdf",
    });
    const url = URL.createObjectURL(blob);
    window.open(url);
  };

  const [open, setOpen] = useState(false);
  const [selectedRow, setSelectedRow] = useState(null);
  const [deleteOpen, setDeleteOpen] = useState(false);

  const handleEdit = (row) => {
    props.initiateEdit();
    setSelectedRow(row);
    setOpen(true);
  };

  const handleDelete = (row) => {
    setSelectedRow(row);
    setDeleteOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedRow(null);
  };

  const handleSubmit = (data) => {
    props.handleUpdate(data);
  };

  const confirmDelete = () => {
    props.handleDelete(selectedRow);
    setDeleteOpen(false);
    setSelectedRow(null);
  };

  const initialState = {
    id: selectedRow?._id ? selectedRow?._id : "",
    companyID: selectedRow?.parttime_id ? selectedRow?.parttime_id?._id : "",
    location: selectedRow?.location ? selectedRow?.location : "",
    shiftDate: selectedRow?.date ? selectedRow?.date : "",
    startTime: selectedRow?.start_time ? selectedRow?.start_time : "",
    endTime: selectedRow?.end_time ? selectedRow?.end_time : "",
  };
  return (
    <>
      <FilterModal handleFilter={props?.handleFilter} />
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 700 }} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>S.No. </StyledTableCell>
              <StyledTableCell>Company Name</StyledTableCell>
              <StyledTableCell align="right">Location</StyledTableCell>
              <StyledTableCell align="right">Date</StyledTableCell>

              <StyledTableCell align="right">Start - End Time</StyledTableCell>
              <StyledTableCell align="right">Total Hours</StyledTableCell>
              <StyledTableCell align="right">Actions</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {props?.ShiftData?.shiftList?.shifts?.map((row, index) => (
              <StyledTableRow key={row._id}>
                <StyledTableCell align="middle">{index + 1}</StyledTableCell>
                <StyledTableCell component="th" scope="row">
                  {row?.parttime_id?.part_time_name}
                </StyledTableCell>
                <StyledTableCell align="right">{row?.location}</StyledTableCell>
                <StyledTableCell align="right">
                  {convertDate(row?.date)}
                </StyledTableCell>
                <StyledTableCell align="right">
                  {getTimeStamp(row?.start_time, row?.end_time)}
                </StyledTableCell>
                <StyledTableCell align="right">
                  {row?.shift_hours}
                </StyledTableCell>
                <StyledTableCell align="right">
                  <IconButton
                    aria-label="delete"
                    size="small"
                    onClick={() => handleEdit(row)}
                  >
                    <EditIcon fontSize="inherit" />
                  </IconButton>
                  <IconButton
                    aria-label="delete"
                    size="small"
                    onClick={() => handleDelete(row)}
                  >
                    <DeleteIcon fontSize="inherit" />
                  </IconButton>
                </StyledTableCell>
              </StyledTableRow>
            ))}
            <TableRow>
              <TableCell rowSpan={3} colSpan={2} />
              <TableCell colSpan={3}>Total Hours</TableCell>
              <TableCell align="right">
                {props?.ShiftData?.shiftList?.totalHours}
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell colSpan={3}>Total Pay (without tax*)</TableCell>
              <TableCell align="right">
                {props?.ShiftData?.shiftList?.totalPay}
              </TableCell>
            </TableRow>
          </TableBody>
          <TablePagination
            rowsPerPageOptions={[5, 10, 25, 50]}
            colSpan={5}
            count={props?.ShiftData?.shiftList?.count}
            rowsPerPage={props?.state?.rowsperpage}
            page={props?.state?.page}
            SelectProps={{
              inputProps: {
                "aria-label": "rows per page",
              },
              native: true,
            }}
            onPageChange={props.handleChangePage}
            onRowsPerPageChange={props.handleChangeRowsPerPage}
            ActionsComponent={(paginationProps) => (
              <TablePaginationActions
                {...paginationProps}
                state={props.state}
                ShiftData={props.ShiftData}
                handleChangePage={props.handleChangePage}
              />
            )}
          />{" "}
        </Table>{" "}
      </TableContainer>
      <Grid container spacing={2} justifyContent="flex-end">
        <Grid
          item
          xs={12}
          md={12}
          sx={{
            display: "flex",
            justifyContent: "flex-end",
            marginTop: "10px",
          }}
        >
          {props?.ShiftData?.shiftList?.shifts?.length > 0 && (
            <PDFDownloadLink
              document={<MyDocument data={props?.ShiftData?.shiftList} />}
              fileName={`shiftReport${new Date()}.pdf`}
            >
              {({ blob, url, loading, error }) =>
                loading ? (
                  "Loading document..."
                ) : (
                  <Button
                    variant="outlined"
                    startIcon={<DownloadIcon />}
                    color="primary"
                  >
                    Download
                  </Button>
                )
              }
            </PDFDownloadLink>
          )}
        </Grid>
      </Grid>
      <Modal
        open={deleteOpen}
        onClose={() => setDeleteOpen(false)}
        aria-labelledby="delete-confirmation-title"
        aria-describedby="delete-confirmation-description"
      >
        <Box sx={style}>
          <Typography variant="h6" id="delete-confirmation-title">
            Confirm Deletion
          </Typography>
          <Typography id="delete-confirmation-description" sx={{ mt: 1 }}>
            Are you sure you want to delete this shift?
          </Typography>
          <Grid
            item
            xs={12}
            md={12}
            sx={{ mt: 2, display: "flex", justifyContent: "space-between" }}
          >
            <Button variant="contained" type="submit" onClick={confirmDelete}>
              Yes, Delete
            </Button>
            <Button variant="outlined" onClick={() => setDeleteOpen(false)}>
              Cancel
            </Button>
          </Grid>
        </Box>
      </Modal>

      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        <Grid container xs={6} md={6} spacing={2} sx={style}>
          {props?.companyData?.isparttimeListFetched && (
            <ShiftForm
              companies={props?.companyData?.parttimeList?.data}
              handleSubmit={handleSubmit}
              initialState={initialState}
              label="Update Shift"
            />
          )}
        </Grid>
      </Modal>
    </>
  );
};
export default ShiftListPage;
