import React from "react";
import {
  Document,
  Page,
  Text,
  View,
  StyleSheet,
  Link,
} from "@react-pdf/renderer";
import { format } from "date-fns";

const styles = StyleSheet.create({
  page: {
    flexDirection: "column",
    padding: 20,
  },
  header: {
    fontSize: 20,
    marginBottom: 20,
    textAlign: "center",
    fontFamily: "Helvetica-Bold",
  },
  table: {
    display: "table",
    width: "auto",
    borderWidth: 1,
  },
  rowHeader: {
    flexDirection: "row",
    borderBottomWidth: 1,
    borderTopWidth: 1,
    alignItems: "center",
    height: 30,
    textAlign: "center",
  },
  row: {
    flexDirection: "row",
    borderBottomWidth: 1,
    alignItems: "center",
    height: 30,
    textAlign: "center",
  },
  headerCell: {
    fontSize: 12,
    fontWeight: "bold",
    borderRightWidth: 1,
  },
  headerCellLast: {
    fontSize: 12,
    fontWeight: "bold",
  },
  cell: {
    fontSize: 12,
    borderRightWidth: 1,
    borderColor: "#ccc",
    flex: 1,
  },
  cellLast: {
    fontSize: 12,
    borderColor: "#ccc",
    flex: 1,
  },
  totalRow: {
    flexDirection: "row",
    borderTopWidth: 1,
    borderColor: "#ccc",
    alignItems: "center",
    height: 30,
    textAlign: "center",
  },
  dateRow: {
    flexDirection: "row",
    border: 1,
    borderColor: "#ccc",
    alignItems: "center",
    height: 30,
    textAlign: "center",
    marginBottom: 20,
  },
  footerStyles: {
    position: "absolute",
    bottom: 30,
    left: 0,
    right: 0,
    textAlign: "center",
    fontSize: 10,
  },
});
const formatTime = (timeString) => {
  const date = new Date(timeString);
  return date.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
};
const PDFGenerator = ({ data }) => {
  return (
    <Document>
      <Page size="A4" style={styles.page}>
        <Text style={styles.header}>Shift Report</Text>
        <View style={styles.dateRow}>
          <Text style={[styles.headerCell, { width: "25%" }]}>From:</Text>
          <Text style={[styles.cell, { width: "25%" }]}>
            {data?.startDate
              ? format(new Date(data?.startDate), "MM-dd-yyyy")
              : format(
                  new Date(data?.shifts[data?.shifts?.length - 1].date),
                  "MM-dd-yyyy"
                )}
          </Text>
          <Text style={[styles.headerCell, { width: "25%" }]}>To:</Text>
          <Text style={[styles.cellLast, { width: "25%" }]}>
            {data?.endDate
              ? format(new Date(data?.endDate), "MM-dd-yyyy")
              : format(new Date(data?.shifts[0].date), "MM-dd-yyyy")}
          </Text>
        </View>
        <View style={styles.table}>
          <View style={styles.rowHeader}>
            <Text style={[styles.headerCell, { width: "20%" }]}>Date</Text>
            <Text style={[styles.headerCell, { width: "20%" }]}>Location</Text>
            <Text style={[styles.headerCell, { width: "20%" }]}>
              Start Time
            </Text>
            <Text style={[styles.headerCell, { width: "20%" }]}>End Time</Text>
            <Text style={[styles.headerCellLast, { width: "20%" }]}>
              Shift Hours
            </Text>
          </View>
          {data?.shifts.map((shift) => (
            <View key={shift._id} style={styles.row}>
              <Text style={[styles.cell, { width: "20%" }]}>
                {format(new Date(shift?.date), "MM-dd-yyyy")}
              </Text>
              <Text style={[styles.cell, { width: "20%" }]}>
                {shift?.location}
              </Text>
              <Text style={[styles.cell, { width: "20%" }]}>
                {formatTime(shift?.start_time)}
              </Text>
              <Text style={[styles.cell, { width: "20%" }]}>
                {formatTime(shift?.end_time)}
              </Text>
              <Text style={[styles.cellLast, { width: "20%" }]}>
                {shift?.shift_hours}
              </Text>
            </View>
          ))}
          <View style={styles.totalRow}>
            <Text style={[styles.headerCell, { width: "80%" }]}>
              Total Hours:
            </Text>
            <Text style={[styles.cell, { width: "20%" }]}>
              {data?.totalHours}
            </Text>
          </View>
          <View style={styles.totalRow}>
            <Text style={[styles.headerCell, { width: "80%" }]}>
              Total Pay(without tax):
            </Text>
            <Text style={[styles.cell, { width: "20%" }]}>
              {data?.totalPay}
            </Text>
          </View>
        </View>
        <View style={styles.footerStyles}>
          <Text>
            Generated by{" "}
            <Link src="https://ptracker.onrender.com" color="blue">
              PTracker
            </Link>
            , designed and developed by{" "}
            <Link src="https://www.linkedin.com/in/udaikiran-ravada/" color="blue">
              Udaikiran
            </Link>
          </Text>
        </View>
      </Page>
    </Document>
  );
};

export default PDFGenerator;
