import React, { useEffect, useState } from "react";
import ShiftListPage from "../components/ShiftListPage";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import { getallShifts, updateShift,deleteShift } from "../store/actions/ShiftActions";
import { getallParttime } from "../store/actions/CompanyActions";
import { useSnackbar } from "notistack";
const ShiftList = (props) => {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [state, setState] = useState({
    rowsperpage: 10,
    page: 0,
  });
  const [filterState, setFilterState] = useState({});

  const getAllShifts = () => {
    const data = {
      userId: props?.userData?.loginData?.user_id,
      limit: state?.rowsperpage,
      page: parseInt(state?.page, 10) + 1,
    };
    props.getallShifts(data);
  };

  const handleFilter = (params) => {
    const data = {
      userId: props?.userData?.loginData?.user_id,
      limit: state?.rowsperpage,
      page: parseInt(state?.page, 10) + 1,
      parttime_id: params?.companyID,
      fromDate: params?.fromDate,
      toDate: params?.toDate,
    };
    setFilterState(data);
    props.getallShifts(data);
  };
  const handleChangePage = (event, newPage) => {
    setState({ ...state, page: newPage });
  };

  const handleChangeRowsPerPage = (event) => {
    setState({
      ...state,
      rowsperpage: parseInt(event.target.value, 10),
      page: 0,
    });
  };

  const initiateEdit = () => {
    const data = {
      userId: props?.userData?.loginData?.user_id,
    };
    props.getallParttime(data);
  };

  const handleDelete = async(data) => {
    try{
      await props.deleteShift(data);
      enqueueSnackbar("Shift deleted Successfully", {
        variant: "success",
      });
      if (
        filterState?.parttime_id ||
        filterState?.fromDate ||
        filterState?.toDate
      ) {
        const data = {
          userId: props?.userData?.loginData?.user_id,
          limit: state?.rowsperpage,
          page: parseInt(state?.page, 10) + 1,
          parttime_id: filterState?.parttime_id,
          fromDate: filterState?.fromDate,
          toDate: filterState?.toDate,
        };
        props.getallShifts(data);
      } else {
        const data = {
          userId: props?.userData?.loginData?.user_id,
          limit: state?.rowsperpage,
          page: parseInt(state?.page, 10) + 1,
        };
        props.getallShifts(data);
      }
    }catch (err) {
      console.log(err);
      enqueueSnackbar(err, { variant: "error" });
    }
  };

  const handleUpdate = async (data) => {
    try {
      await props.updateShift(data);
      enqueueSnackbar("Shift updated Successfully", {
        variant: "success",
      });
      if (
        filterState?.parttime_id ||
        filterState?.fromDate ||
        filterState?.toDate
      ) {
        const data = {
          userId: props?.userData?.loginData?.user_id,
          limit: state?.rowsperpage,
          page: parseInt(state?.page, 10) + 1,
          parttime_id: filterState?.parttime_id,
          fromDate: filterState?.fromDate,
          toDate: filterState?.toDate,
        };
        props.getallShifts(data);
      } else {
        const data = {
          userId: props?.userData?.loginData?.user_id,
          limit: state?.rowsperpage,
          page: parseInt(state?.page, 10) + 1,
        };
        props.getallShifts(data);
      }
    } catch (err) {
      console.log(err);
      enqueueSnackbar(err, { variant: "error" });
    }
  };

  useEffect(() => {
    getAllShifts();
  }, [state]);
  return (
    props?.ShiftData?.isshiftListFetched && (
      <ShiftListPage
        navigate={navigate}
        ShiftData={props?.ShiftData}
        companyData={props?.companyData}
        handleChangePage={handleChangePage}
        state={state}
        handleChangeRowsPerPage={handleChangeRowsPerPage}
        handleUpdate={handleUpdate}
        handleFilter={handleFilter}
        handleDelete={handleDelete}
        initiateEdit={initiateEdit}
      />
    )
  );
};

function mapStateToProps(state) {
  return {
    userData: state.LoginReducer,
    companyData: state.CompanyReducer,
    ShiftData: state.ShiftReducer,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    getallShifts: (params) => dispatch(getallShifts(params)),
    getallParttime: (params) => dispatch(getallParttime(params)),
    updateShift: (params) => dispatch(updateShift(params)),
    deleteShift:(params) => dispatch(deleteShift(params)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ShiftList);
