import { useState } from "react";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle"; // Adjust the path
import { Field, Formik, Form } from "formik";
import FilterListIcon from "@mui/icons-material/FilterList";
import { DropDownField } from "../common/DropDownField";
import MobileDatePicker from "../common/MobileDatePicker";
import { connect } from "react-redux";
import { Grid, Button, InputLabel, FormControl, MenuItem } from "@mui/material";
import { getallParttime } from "../store/actions/CompanyActions";
const FilterModal = (props) => {
  console.log(props);
  const [open, setOpen] = useState(false);
  const initialState = {
    companyID: "",
    location: "",
    fromDate: "",
    toDate: "",
  };

  const handleOpen = () => {
    const data = {
      userId: props?.userData?.loginData?.user_id,
    };
    props.getallParttime(data);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleApplyFilter = (filterData) => {
    handleClose();
    props.onApplyFilter(filterData);
  };

  return (
    <div>
      <Grid container spacing={2} justifyContent="flex-end">
        <Grid
          item
          xs={12}
          md={12}
          sx={{
            display: "flex",
            justifyContent: "flex-end",
            marginBottom: "10px",
          }}
        >
          <Button
            variant="outlined"
            startIcon={<FilterListIcon />}
            color="primary"
            onClick={handleOpen}
          >
            Filter
          </Button>
        </Grid>
      </Grid>
      <Dialog open={open} onClose={handleClose} fullWidth maxWidth="md">
        <DialogTitle>Apply Filters</DialogTitle>
        <DialogContent>
          {props?.companyData?.isparttimeListFetched && (
            <Formik
              initialValues={initialState}
              onSubmit={async (
                values,
                { setFieldValue, setSubmitting, resetForm }
              ) => {
                console.log("clicked");
                try {
                  props?.handleFilter(values);
                  setSubmitting(false);
                  resetForm();
                } catch (error) {
                  console.error(error);
                  setSubmitting(false); // Ensure that form submission is stopped on error
                }
              }}
            >
              {({ values, setFieldValue, touched, errors }) => {
                return (
                  <Form>
                    <Grid
                      container
                      spacing={2}
                      mt={1}
                      sx={{
                        width: "100%",
                        border: "1px solid lightgrey",
                        borderRadius: "5px",
                        padding: "1rem 1rem 1rem 0",
                        marginLeft: "0",
                      }}
                    >
                      <Grid item md={12} xs={12} sx={{ marginTop: "-1rem" }}>
                        <Grid container spacing={2}>
                          <Grid item xs={12} md={12}>
                            <FormControl fullWidth>
                              <InputLabel htmlFor="selectLanguage">
                                Select Company
                              </InputLabel>
                              <Field
                                name={`companyID`}
                                value={`${values.companyID}`}
                                label="Company Name"
                                placeholder="Company Name"
                                component={DropDownField}
                                //error={
                                //touchedItems?.apartmentUnitHouseNumber &&
                                //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                                //}
                              >
                                {props?.companyData?.parttimeList?.data?.map(
                                  (data, index) => (
                                    <MenuItem
                                      key={index}
                                      onClick={() => {
                                        setFieldValue("companyID", data?._id);
                                      }}
                                      value={data?._id}
                                    >
                                      {data?.part_time_name}
                                    </MenuItem>
                                  )
                                )}
                              </Field>{" "}
                            </FormControl>
                          </Grid>
                          <Grid item xs={12} md={12}>
                            <MobileDatePicker
                              views={["year", "month", "day"]}
                              inputFormat="MM/dd/yyyy"
                              name={`fromDate`}
                              label="From Date"
                              setFormikFieldvalue={setFieldValue}
                              endAdornmentRequired={true}
                              maxDate={new Date()}
                            />
                          </Grid>
                          <Grid item xs={12} md={12}>
                            <MobileDatePicker
                              views={["year", "month", "day"]}
                              inputFormat="MM/dd/yyyy"
                              name={`toDate`}
                              label="To Date"
                              setFormikFieldvalue={setFieldValue}
                              endAdornmentRequired={true}
                              maxDate={new Date()}
                            />
                          </Grid>
                          <Grid
                            item
                            xs={12}
                            md={12}
                            sx={{
                              display: "flex",
                              justifyContent: "center",
                              alignItems: "center",
                            }}
                          >
                            <Button
                              sx={{
                                width: "200px",
                                height: "39px",
                                marginRight: "10px",
                              }}
                              variant="contained"
                              onClick={handleClose}
                            >
                              close
                            </Button>
                            <Button
                              sx={{
                                width: "200px",
                                height: "39px",
                              }}
                              variant="contained"
                              type="submit"
                            >
                              apply
                            </Button>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                  </Form>
                );
              }}
            </Formik>
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
};

function mapStateToProps(state) {
  return {
    userData: state.LoginReducer,
    companyData: state.CompanyReducer,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    getallParttime: (params) => dispatch(getallParttime(params)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(FilterModal);
